import React from "react";
import { Link } from "react-router-dom";
import Private from "../../services/Private.service";
import { withAuth } from "../../context/auth.context";
import RoundButton from "../../components/resources/RoundButton/RoundButton";
import "./Profile.css";
import { Button, Modal } from "react-bootstrap";
import Card from "react-bootstrap/Card";

class Profile extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      user: {
        fav: [],
      },
    };

    this.Private = new Private();
  }

  DeletOne(userId) {
    this.Private.deleteOne(userId)
      .then((user) => {
        console.log(user);
        this.forceUpdate();
      })
      .catch((error) => console.log(error));
  }

  displayFavorites() {
    console.log(this.state.user);
    return this.state.user.fav.map((fav) => {
      return (
        <div className="marginfav">
          <img className="imagefav" src={fav.img} alt={fav.img} />
          <p className="nameFav">{fav.name}</p>
                 <div className="form-check">
                 <RoundButton>
            <Link className="details" to={"profile/" + fav._id}>More details</Link>
            </RoundButton>
            <p>&nbsp;</p>
            <RoundButton>
            <p onClick={() => this.DeletOne(fav._id)}>
              Delete Contact
            </p>
            </RoundButton>
          </div>
        </div>
      );
    });
  }

  componentDidMount() {
    const id = this.props.match.params.id;
    new Private()
      .getFav({ id: this.props.user.id })
      .then((response) => {
        console.log(response);
        this.setState({
          user: response.data,
        });
      })
      .catch((error) => {
        console.log(error);
      });
  }

  render() {
    return (
      <>
      <div className="signin-block">
        <div className="container">
          <Card.Body>
          <p>&nbsp;</p>
            <p className="userName">Welcome {this.props.user.username}</p>
            <p>&nbsp;</p>
            <img
              className="imgage"
              src={this.props.user.img}
              alt={this.props.user.name}
            />

            <p className="list-group-flush">
              <p className="nameFav">{this.props.user.city}</p>
              <p className="nameFav">{this.props.user.about} </p>
              <p className="nameFav">My contacts:</p>
              <div className="gridfav">
              {this.displayFavorites()}
              </div>
            </p>
          </Card.Body>
          <displayFavorites />
          <div className="form-check">
            <Link to="/profile/edit">
              <Button className="button--primary mt-3 mx-2">
                Edit profile
              </Button>
            </Link>
          </div>
          <div>
            <a onClick={() => this.Deletone()}>DeleteOne</a>
          </div>
        </div>
      </div>
      </>
    );
  }
}

export default withAuth(Profile);
