import React, { Component } from "react";
import uSerService from "../../services/Users.service";
import { Link } from "react-router-dom";
import "./UserList.css";
export default class userList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [],
    };
    this.uSerService = new uSerService();
  }

  refreshState() {
    this.uSerService
      .get()
      .then((response) => {
        console.log(response.data);
        this.setState({ users: response.data });
      })
      .catch((err) => console.error(err));
  }

  componentDidMount() {
    this.refreshState();
  }

  displayUsers() {
    return this.state.users.map((users) => {
      return (
        <div className="border">
          <div className="container">
            <img
              classname="userimag"
              src={users.img}
              max-width="100px"
              width="290"
              height="none"
              alt="Image"
            ></img>
            <div class="card-body">
              <p class="title is-4 no-padding">{users.name}</p>
              <span class="title is-6">
                <p> {users.email} </p>
                <p>{users.city}</p>
                <p>{users.about}</p>
              </span>
            </div>
            <Link to={"userList/" + users._id}>go to profile</Link>
          </div>
        </div>
      );
    });
  }

  render() {
    return <div className="signin-block">{this.displayUsers()}</div>;
  }
}
