import React from "react";
import RoundButton from "../../components/resources/RoundButton/RoundButton";
import { Link } from "react-router-dom";
import Private from "../../services/Private.service";
import { withAuth } from "../../context/auth.context";
import { withRouter } from "react-router";
import UsersService from "../../services/Users.service";
import "./UserDetail.css";

class UserDetail extends React.Component {
  state = { userdata: {} };

  usersService = new UsersService();

  componentDidMount() {
    const id = this.props.match.params.id;
    this.usersService
      .getOne(id)
      .then((user) => {
        this.setState({
          userdata: user.data,
        });
      })
      .catch((error) => console.log(error));
  }

  Private = new Private();

  addOne() {
    const id = this.props.match.params.id;
    this.Private.updateOne(id)
      .then((user) => {
        console.log(user);
      })
      .catch((error) => console.log(error));
  }

  render() {
    const { _id, name, email, city, img, about } = this.state.userdata;
    return (
      <div className="signin-block">
        <div className="container">
          <p>&nbsp;</p>
          <p className="userName">{name}</p>
          <p>&nbsp;</p>
          <img className="imgage" src={img} alt={img} />
          <p>&nbsp;</p>
          <p className="userEmail">{email}</p>
          <p className="userCity">{city}</p>
          <p className="userDescription">{about}</p>
          <p>&nbsp;</p>
          <div classname="buttonUser">
            <RoundButton>
              <p onClick={() => this.addOne()}>add one</p>
            </RoundButton>
            <p>&nbsp;</p>
          </div>
        </div>
      </div>
    );
  }
}

export default withAuth(withRouter(UserDetail));
